// white theme
.j-wrapper.white {
  background: #f0f2f7;

  div.ant-modal.j-modal {
    .ant-tabs-tab {
      border-bottom: 1px solid #f0f0f0 !important;
    }
    .ant-modal-header,
    .ant-modal-body {
      background-color: #ffffff;

      .ant-modal-title {
        color: #22232b;
      }
    }
    .anticon-close {
      background-image: url(../../images/v2/white-theme/modal-close.svg);
    }
    .j-supply {
      .ant-tabs-tab {
        &:nth-child(1) {
          &.ant-tabs-tab-active {
            //border-bottom-color: #4c54ff !important;
            border-bottom: 2px solid #4c54ff !important;
            .ant-tabs-tab-btn {
              color: #4c54ff;
            }
          }
        }
        &:nth-child(2) {
          &.ant-tabs-tab-active {
            // border-bottom-color: #6644c9 !important;
            border-bottom: 2px solid #6644c9 !important;
            .ant-tabs-tab-btn {
              color: #6644c9;
            }
          }
        }
      }
    }
    .j-borrow {
      .ant-tabs-tab {
        &:nth-child(1) {
          &.ant-tabs-tab-active {
            // border-bottom-color: #18c19f !important;
            border-bottom: 2px solid #18c19f !important;
            .ant-tabs-tab-btn {
              color: #18c19f;
            }
          }
        }
        &:nth-child(2) {
          &.ant-tabs-tab-active {
            // border-bottom-color: #e98061 !important;
            border-bottom: 2px solid #e98061 !important;
            .ant-tabs-tab-btn {
              color: #e98061;
            }
          }
        }
      }
    }
    .j-modal-ele {
      > span,
      > span > span {
        color: rgba(34, 35, 43, 0.6);
      }
      .j-content {
        color: #22232b;
      }
      .j-title {
        color: rgba(34, 35, 43, 0.6);
      }
    }
    .arrow-right {
      background-image: url(../../images/v2/white-theme/arrow-right.svg);
    }
    .borrow-tip {
      // background-color: rgba(75, 82, 223, 0.04);
      color: rgba(34, 35, 43, 0.6);
      > span {
        color: rgba(34, 35, 43, 0.6);
        &:first-child {
          background-image: url(../../images/v2/white-theme/warning-yellow-icon.svg);
        }
      }
      > a {
        color: #4c54ff;
      }
    }
    &.header-border {
      .ant-modal-header {
        border-bottom-color: rgba(34, 35, 43, 0.06);
      }
    }
    .collateral-limit-img {
      margin: 50px auto 20px;
      padding: 0;
      background: rgba(75, 82, 223, 0.04);
      border-radius: 50%;
    }
    .collateral-limit {
      color: #22232b;
    }
    &.ant-modal.j-mortgage-modal {
      .borrow-using {
        .cancel-img {
          background-image: url(../../images/v2/white-theme/fail.png);
        }
        > span {
          color: #22232b;
        }
      }
      .j-values {
        color: #22232b;
      }
      .tips,
      .content .r1,
      .content .r2,
      .old-value {
        color: rgba(34, 35, 43, 0.8);
      }
      .content {
        background: rgba(75, 82, 223, 0.04);
      }
    }

    .j-wallet > span {
      color: #22232b;
      &:first-child {
        color: rgba(34, 35, 43, 0.8);
      }
    }
  }
  .j-light {
    background-image: url(../../images/v2/white-theme/light-yellow.svg);
  }
  .warning-icon {
    background-image: url(../../images/v2/white-theme/warning-arrow-icon.svg);
  }
  .j-header {
    background: #fcfcfe;
    .j-feedback {
      color: #4c54ff;
    }
  }
  .j-logo {
    background-image: url(../../images/v2/white-theme/logo.svg);
  }
  .j-theme-logo {
    width: 13px;
    height: 14px;
    background-image: url(../../images/v2/white-theme/theme-logo.svg);
  }
  .j-links a {
    color: rgba(34, 35, 43, 0.6);
    &.current,
    &:hover {
      color: #22232b;
    }
  }
  .j-wallet-info {
    background: rgba(75, 82, 223, 0.06);

    &.market {
      background: none;
      border: 1px solid rgba(15, 19, 79, 0.1);
      border-radius: 4px;
    }

    > span {
      color: rgba(34, 35, 43, 0.6);
    }
    &:hover {
      background: rgba(75, 82, 223, 0.1);
      > span {
        color: #4c54ff;
      }
    }
  }
  .j-subhead {
    .j-title {
      > span,
      > a {
        color: #22232b;
      }
      > a {
        background: #fff;
        &:hover {
          color: #4d54ff;
        }
      }
    }
    > div {
      &:last-child {
        &:hover {
          .announce-content {
            color: #4c54ff;
          }
        }
      }
    }
  }
  .j-announce {
    .announce-icon {
      background-image: url(../../images/v2/white-theme/announce-icon.svg);
    }
    .announce-arrow-icon {
      background-image: url(../../images/v2/white-theme/announce-arrow-icon.svg);
    }
    .announce-content {
      color: #22232b;
    }
  }
  .j-safe-tip {
    background: #f8f8fe;

    > div > span {
      color: rgba(34, 35, 43, 0.6);
    }
    .j-safe-img {
      background-image: url(../../images/v2/white-theme/light-yellow.svg);
    }
    .j-safe-text,
    .j-safe-text span {
      color: rgba(34, 35, 43, 0.6);
    }

    .j-safe-text .safe-fee {
      color: #4c54ff;
    }
  }
  .j-error-tip {
    background: #f8f8fe;
    .j-error-img {
      background-image: url(../../images/v2/white-theme/warning-icon.svg);
    }
  }
  .j-ele {
    background-color: #ffffff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
  }
  .nc-header {
    color: #22232b;
  }
  .nc-progress {
    .nc-line {
      border-bottom: 1px dashed rgba(34, 35, 43, 0.2);
    }

    .progress-ele::after {
      border-color: rgba(34, 35, 43, 0.2);
    }
    .progress-img {
      background: transparent;
      background-size: 100%;
      &.progress1 {
        background-image: url(../../images/v2/white-theme/progress1.png);
      }
      &.progress2 {
        background-image: url(../../images/v2/white-theme/progress2.png);
      }
      &.progress3 {
        background-image: url(../../images/v2/white-theme/progress3.png);
      }
      &.progress4 {
        background-image: url(../../images/v2/white-theme/progress4.png);
      }
    }
    .progress-text {
      color: rgba(34, 35, 43, 0.6);
    }
    .progress-ele {
      background: #fff;
    }
  }
  .clear-icon {
    background-image: url(../../images/v2/white-theme/clear-icon.svg);
  }
  .j-recommend {
    background-image: url(../../images/v2/white-theme/recommend/usdd.png);

    &.trx {
      background-image: url(../../images/v2/white-theme/recommend/trx.png);
    }

    &.btc {
      background-image: url(../../images/v2/white-theme/recommend/btc.png);
    }

    &.btt {
      background-image: url(../../images/v2/white-theme/recommend/btt.png);
    }

    &.eth {
      background-image: url(../../images/v2/white-theme/recommend/eth.png);
    }

    &.jst {
      background-image: url(../../images/v2/white-theme/recommend/jst.png);
    }

    &.nft {
      background-image: url(../../images/v2/white-theme/recommend/nft.png);
    }

    &.sun {
      background-image: url(../../images/v2/white-theme/recommend/sun.png);
    }

    &.tusd {
      background-image: url(../../images/v2/white-theme/recommend/tusd.png);
    }

    &.usdc {
      background-image: url(../../images/v2/white-theme/recommend/usdc.png);
    }

    &.usdd {
      background-image: url(../../images/v2/white-theme/recommend/usdd.png);
    }

    &.usdj {
      background-image: url(../../images/v2/white-theme/recommend/usdj.png);
    }

    &.usdt {
      background-image: url(../../images/v2/white-theme/recommend/usdt.png);
    }

    &.wbtt {
      background-image: url(../../images/v2/white-theme/recommend/wbtt.png);
    }

    &.win {
      background-image: url(../../images/v2/white-theme/recommend/win.png);
    }

    .recommend-info > div {
      &:nth-child(1) {
        color: rgba(34, 35, 43, 0.4);
      }
      &:nth-child(2) {
        color: #22232b;
      }
    }
    .recommend-token {
      .recommend-ele {
        background: rgba(34, 35, 43, 0.04);
        color: rgba(34, 35, 43, 0.4);
      }
      .token-text {
        color: #22232b;
      }
      .recommend-img {
        background-image: url(../../images/v2/white-theme/star.svg);
      }
    }
  }

  .j-records {
    background: #ffffff;

    .j-tabs {
      border-bottom-color: rgba(34, 35, 43, 0.1);
      > div {
        > span {
          &:first-child {
            color: rgba(34, 35, 43, 0.4);
          }
        }

        &.current {
          > span:first-child {
            color: #22232b;
          }
          .j-num {
            color: #fff;
          }
        }
      }
    }
    .j-list-content {
      > div {
        &::-webkit-scrollbar {
          width: 1px;
        }
        &::-webkit-scrollbar-track {
          background: #fff;
          border-radius: 2px;
        }

        &::-webkit-scrollbar-thumb {
          background: #ccc;
          border-radius: 13px;
        }
      }
      .linear {
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.91) 59.67%, #ffffff 100%);
      }
    }
    .j-warning-tip {
      background: #fff5f6;
      > div {
        color: rgba(34, 35, 43, 0.6);
      }
      .icon {
        background-image: url(../../images/v2/white-theme/light-yellow.svg);
      }
    }
  }
  .bs-list {
    .ant-table-empty .ant-table-tbody > tr:hover td {
      background-color: #fff !important;
    }
    .ant-table-thead > tr > th {
      background: #f8f8ff;
    }
    .ant-table-tbody > tr > td {
      background: #ffffff !important;
    }
    .ant-table-tbody > tr.ant-table-row:hover > td {
      background: #f6f6f7 !important;
    }
    .ant-table-tbody {
      tr:hover {
        td {
          background: #f6f6f7 !important;
        }
      }
    }
    .ant-table-thead > tr > th {
      color: #76777f;
      font-weight: 500 !important;
    }
    .ant-table-tbody > tr > td {
      color: #22232b;
    }
    .sub-desc {
      color: rgba(34, 35, 43, 0.6);
    }
    .new-switch {
      .switch-line {
        background: #d0d0d6;
      }
      &.switch-on .switch-line {
        background: #4d54ff;
      }
      .switch-circle {
        box-shadow: 0px 0px 5px rgb(99 97 97 / 30%);
      }
    }
    &.j-borrow-list {
      .ant-table-thead > tr > th {
        background: #f6fdfb;
      }
      .ant-table-tbody > tr:hover:not(.ant-table-expanded-row) > td,
      .ant-table-tbody .ant-table-row-hover,
      .ant-table-tbody .ant-table-row-hover > td,
      .ant-table-content .ant-table-thead th.ant-table-column-has-sorters:hover {
        background-color: #f6fdfb;
      }
    }
  }
  .j-btn {
    &.j-supply {
      color: #4d54ff;
      background: rgba(77, 84, 255, 0.06);
      &:hover,
      &:focus {
        color: #ffffff;
        background: #4c54ff;
      }
    }
    &.j-borrow {
      color: #18c19f;
      background: rgba(24, 193, 159, 0.06);
      &:hover,
      &:focus {
        color: #ffffff;
        background: #18c19f;
      }
    }
    &.j-repay {
      color: #e98061;
      background: rgba(233, 128, 97, 0.06);
      border: 1px solid rgba(233, 128, 97, 0.6);
      &:hover,
      &:focus {
        color: #ffffff;
        background: #e98061;
      }
    }
    &.j-reward {
      color: rgba(34, 35, 43, 0.6);
      background: #edeefc;
      &:hover {
        color: #4c54ff;
      }
    }
  }
  .j-mining-row {
    .mining-tip {
      &::before {
        background: rgba(34, 35, 43, 0.1);
      }
      > span {
        background: #fff;
        color: rgba(34, 35, 43, 0.4);
      }
    }
    .row-symbol > div {
      &:first-child {
        color: #22232b;
      }
      &:nth-child(2) {
        color: rgba(34, 35, 43, 0.6);
        background: rgba(34, 35, 43, 0.06);
      }
    }
    .row-token {
      color: rgba(34, 35, 43, 0.6);
    }
  }
  .j-tooltip-icon {
    background-image: url(../../images/v2/white-theme/tooltip-icon.svg);
  }
  .ant-table-column-sorter-up,
  .ant-table-column-sorter-down {
    background-image: url(../../images/v2/white-theme/sort-arrow.svg);
  }
  .ant-table-column-sorter-up.active,
  .ant-table-column-sorter-down.active {
    background-image: url(../../images/v2/white-theme/sort-arrow-active.svg);
  }
  .ant-select-dropdown.select-max-dropdown {
    padding-top: 16px;
    padding-bottom: 0;
    background: transparent;
    box-shadow: none;
    .rc-virtual-list {
      background-color: #f8f8fe;
    }
    .rc-virtual-list-holder-inner {
      padding: 20px;
      backdrop-filter: blur(35px);
      border-radius: 2px;
      overflow: hidden;
      > div {
        min-height: auto;
        padding: 0;
        margin-bottom: 10px;
        background-color: transparent;

        &:last-child {
          margin-bottom: 0;
        }
        // &.ant-select-item-option-active:not(.ant-select-item-option-disabled),
        &.ant-select-item-option-selected {
          background-color: transparent;
          .ant-select-item-option-content {
            color: #4d54ff;
          }
        }
        &:hover {
          .ant-select-item-option-content {
            color: #4d54ff;
          }
        }

        .ant-select-item-option-content {
          font-weight: 500;
          font-size: 12px;
          color: rgba(255, 255, 255, 0.6);
          text-align: center;
        }
      }
    }
  }

  .ant-select-dropdown.select-max-dropdown .rc-virtual-list-holder-inner > div .ant-select-item-option-content {
    color: rgba(34, 35, 43, 0.6);
  }
  .ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
    color: #4d54ff;
  }

  // account
  .account-v2-container {
    color: #22232b;
    .account-main-info {
      background: #fff;
      box-shadow: 0px 0.694vw 1.388vw rgba(0, 0, 0, 0.05);
      .content {
        color: #22232b;
        text-shadow: none;
      }
    }
    .more {
      background: #fff url(../../images/v2/white-theme/account/reward-bg.png) right 0 bottom -13px no-repeat;

      background-size: 185px 95px;
      box-shadow: 0px 0.694vw 1.388vw rgba(0, 0, 0, 0.05); // 0 10 20

      &.only-recommend {
        background: #fff url(../../images/v2/white-theme/recommend/mining.png) right bottom no-repeat;
        background-size: 100%;
      }

      .recommend-tip {
        background: linear-gradient(90.13deg, #4c54ff 2.89%, #18c19f 101.53%);
        background-clip: text;
      }
      .countup-int,
      .countup-int .value-number {
        color: #22232b;
      }
    }
    .data-info-key {
      color: rgba(34, 35, 43, 0.6);
    }
    .countup-decimal,
    .countup-decimal .value-number,
    .countup-int .value-number {
      color: #22232b;
    }
    .countup-int,
    .countup-int .value-number {
      color: #22232b;
    }

    .countDatas {
      .btn {
        background: rgba(75, 82, 223, 0.06);
        color: rgba(34, 35, 43, 0.6);
        &:hover {
          background: rgba(75, 82, 223, 0.16);
          color: #4b52df;
        }
      }
    }
  }

  // 

  .ant-tabs-tab-btn {
    color: rgba(34, 35, 43, 0.4);
  }
  .approve-content .approve-tip {
    color: #22232b;
  }

  .approve-content .approve-apy {
    border-top: 1px solid rgba(34, 35, 43, 0.06);
  }
  .approve-content .img {
    background-color: rgba(75, 82, 223, 0.04);
    img {
      width: 63px;
      margin-top: 12px;
      margin-left: 5px;
    }
  }

  .j-input {
    .ant-input,
    .ant-input-group-addon,
    .ant-input[disabled] {
      color: #22232b;
    }
    .ant-input-clear-icon {
      background-image: url(../../images/v2/white-theme/clear-icon.svg);
    }
    .ant-input-group-addon::after {
      background-color: rgba(40, 41, 49, 0.1);
    }

    .ant-input-group-addon,
    .ant-input-affix-wrapper {
      background-color: rgba(75, 82, 223, 0.04);
    }
  }

  .ant-input-group-addon {
    .pointer {
      &:hover {
        color: #4c54ff;
      }
    }
  }

  .j-progress {
    background-color: rgba(40, 41, 49, 0.06);
  }

  .ant-btn.j-large-btn,
  .j-large-btn {
    color: #ffffff !important;
    &:hover,
    &:focus,
    &:active {
      color: #fff;
    }
    &.j-supply {
      background: #4c54ff;
      &:hover {
        background: #9195fb;
      }
      &:focus,
      &:active {
        background: #4c54ff;
      }
    }
    &.j-withdraw {
      background: #6644c9;
      &:hover {
        background: #c1abff;
      }
      &:focus,
      &:active {
        background: #6644c9;
      }
    }
    &.j-borrow {
      background: #18c19f;
      &:hover {
        background: #74dac5;
      }
      &:focus,
      &:active {
        background: #18c19f;
      }
    }
    &.j-repay {
      background: #e98061;
      &:hover {
        background: #f1ac98;
      }
      &:focus,
      &:active {
        background: #e98061;
      }
    }
    &:disabled,
    &:disabled:hover {
      color: rgba(255, 255, 255, 0.6);
      background: #d3d3d5;
    }
  }

  // 
  .ant-select.select-max {
    &.ant-select-open {
      .ant-select-selection-item {
        color: #4c54ff;
      }
      .ant-select-arrow {
        .anticon-down {
          background-image: url(.././../images/v2/white-theme/arrow-blue.svg);
        }
      }
    }
    .ant-select-selection-item {
      color: #22232b;
    }
    .ant-select-arrow {
      .anticon-down {
        background: url(.././../images/v2/white-theme/arrow-black.svg) 0 0 no-repeat;
      }
    }
  }

  .j-trans-info {
    background: rgba(75, 82, 223, 0.04);
    .j-ti-eles .j-ti {
      > span {
        color: rgba(34, 35, 43, 0.8);
        &:last-child {
          color: #22232b;
        }
        > span {
          color: rgba(34, 35, 43, 0.8);
        }
      }
      .j-unit {
        color: #22232b;
      }
    }
    .j-ti-collateral {
      color: #4c54ff;
      border-top-color: rgba(34, 35, 43, 0.06);
    }
  }

  .j-records .j-list-content .horizontal-line {
    background: rgba(34, 35, 43, 0.1);
  }

  .collateral-transaction {
    > div {
      &:nth-child(1) {
        border: 1px solid #4c54ff;
        color: #4c54ff;
      }
    }
  }
  .j-warning-icon {
    background-image: url(../../images/v2/white-theme/warning-yellow-icon.svg);
  }
  .j-reward-unit {
    color: #22232b;
  }

  .scroll-bar {
    border-right-color: #f8f8fe;
    &::-webkit-scrollbar {
      /**/
      width: 1px; /**/
      height: 40px;
      display: none;
    }
    &::-webkit-scrollbar-thumb {
      /**/
      max-height: 40px;
      border-radius: 1px;
      background: #ccc;
    }

    &::-webkit-scrollbar-track {
      background: #fff;
      border-radius: 2px;
    }

    &:hover {
      border-right: none;
      &::-webkit-scrollbar {
        display: block;
      }
    }
  }
  .linear {
    background: linear-gradient(180deg, rgba(248, 248, 254, 0) 0%, #f8f8fe 100%);
  }
  .j-reward:disabled {
    color: rgba(34, 35, 43, 0.3);
    .j-reward-btn-icon {
      background-image: url(../../images/v2/white-theme/account/reward-btn-disabled-icon.svg);
    }
    &:hover {
      color: rgba(34, 35, 43, 0.3);
    }
  }
}
.ant-modal.account-invest-modal.white {
  background-color: #ffffff;
  &.reward {
    .current-income-val {
      color: #4c54ff !important;
    }
    .j-accruing {
      color: #22232b;
    }
  }
  .ant-modal-content,
  .ant-modal-header,
  .ant-modal-body {
    background-color: #ffffff;
  }
  .ant-modal-header {
    border-bottom: 1px solid rgba(34, 35, 43, 0.06);

    .ant-modal-title {
      color: #22232b;
    }
  }
  .table-nodata {
    background: rgba(75, 82, 223, 0.04);
  }
  .table-nodata p {
    color: rgba(34, 35, 43, 0.8);
  }
  .user-invest-modal-info.no-data p,
  .ant-table-container .ant-table-tbody > tr td .white12,
