.ant-modal.connect-modal {
  width: 90% !important;

  .ant-modal-header {
    border-bottom: 1px solid rgba(15, 19, 79, 0.05);
  }

  .ant-modal-close-x {
    right: 0;
    top: 0;
    width: 56px !important;
    height: 56px !important;
    background: url(../images/mobile/close-icon.svg) center no-repeat;
    background-size: 12px 12px;
  }
}

.connect-element {
  padding: 30px 0 0;
}

.entry-modal {
  max-width: 345px !important;

  .ant-modal-body {
    padding: 0 10px 30px;
  }
}

.entry-ele {
  .top-tip {
    font-family: 'PingFang SC';
    font-size: 12px;
    line-height: 20px;
    color: #0f134f;
    text-align: center;
    margin-bottom: 20px;
  }

  .bottom-tip {
    // display: flex;
    // justify-content: center;
    //align-items: center;
    text-align: center;

    > a,
    > span {
      // display: inline-block;
      font-family: 'PingFang SC';
      font-weight: 400;
      font-size: 12px;
      border-bottom: 1px solid transparent;
      padding-bottom: 3px;
      line-height: 17px;
    }

    > span {
      color: #5a5e89;
    }

    > a {
      color: #3d56d6;
      margin-left: 4px;
      position: relative;

      &::after {
        content: '';
        width: 0;
        height: 1px;
        background: #3d56d6;
        position: absolute;
        top: 100%;
        left: 50%;
        transition: all 0.6s;
      }

      &:hover::after {
        left: 0%;
        width: 100%;
      }
    }
  }

  .entry-logos {
    margin-bottom: 10px;

    .entry-logo {
      width: 285px;
      height: 50px;
      background: rgba(15, 19, 79, 0.05);
      border-radius: 8px;
      cursor: pointer;
      padding: 12px 20px;
      margin: 0 auto 10px;
      position: relative;

      &:hover {
        background: rgba(19, 93, 205, 0.1);
      }

      &.disabled {
        cursor: default;
        background: rgba(15, 19, 79, 0.05);
      }
    }

    .entry-logo {
      > img {
        width: 26px;
        height: 26px;
        margin-bottom: 10px;
      }

      > span {
        font-family: 'DM Sans';
        font-style: normal;
        font-size: 14px;
        line-height: 26px;
        text-align: center;
        color: #0f134f;
        margin-left: 14px;
        display: inline-block;
        vertical-align: top;
      }

      .entry-beta {
        height: 20px;
        line-height: 20px;
        padding: 0 10px;
        margin: 0 10px;
        font-weight: 500;
        font-size: 12px;
        color: #18c19f;
        background: rgba(24, 193, 159, 0.2);
      }

      .loading {
        width: 20px;
        height: 20px;
        transform: rotate(360deg);
        animation: rotation 2s linear infinite;
        position: absolute;
        top: 15px;
        right: 20px;
      }
    }
  }

  @keyframes rotation {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
}

.fireblocks-modal {
  max-width: 480px !important;
  position: relative;

  .ant-modal-body {
    padding: 0 20px 20px;
  }

  p {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #0f134f;
    text-align: center;
    margin-bottom: 10px;
  }

  .top-tip {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: rgba(90, 94, 137, 0.8);
    margin-bottom: 20px;
    white-space: nowrap;

    > img {
      width: 14px;
      height: 14px;
      margin: -1px 5px 0;
    }

    span {
      font-size: 12px;
    }

    strong {
      font-size: 12px;
      font-weight: 400;
    }
  }

  .fireblocks-code {
    text-align: center;
    margin-bottom: 20px;

    > img {
      max-width: 390px;
      width: 100%;
      box-shadow: 0px 0px 20px rgba(15, 19, 79, 0.05);
      border-radius: 8px;
    }
  }

  .bottom-tip {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'PingFang SC';
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #5a5e89;
    white-space: nowrap;

    > span {
      color: #3d56d6;
      cursor: pointer;
      margin-left: 2px;
      border-bottom: 1px solid transparent;
      position: relative;

      &::after {
        content: '';
        width: 0;
        height: 1px;
        background: #3d56d6;
        position: absolute;
        top: 100%;
        left: 50%;
        transition: all 0.6s;
      }

      &:hover::after {
        left: 0%;
        width: 100%;
      }
    }
  }

  .copied {
    bottom: -50px;
  }
}

