.loading-modal {
  max-width: 345px !important;

  .ant-modal-content {
    height: 340px;
  }

  @keyframes rotation {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }

  img {
    width: 60px;
    height: 60px;
    margin: 10px auto 20px;
    display: block;
    transform: rotate(360deg);
    animation: rotation 2s linear infinite;
  }

  .title {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #0f134f;
    height: 25px;
    margin-bottom: 10px;
  }

  .desc {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    height: 20px;
    color: #5a5e89;
    text-align: center;
  }

  .tips {
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    text-align: center;
    color: rgba(90, 94, 137, 0.5);
    position: absolute;
    bottom: 20px;
    width: calc(100% - 48px);

    span {
      font-size: 12px;
    }

    a {
      color: #3d56d6;
      font-size: 12px;
      position: relative;

      &::after {
        content: '';
        width: 0;
        height: 1px;
        background: #3d56d6;
        position: absolute;
        top: 100%;
        left: 50%;
        transition: all 0.8s;
      }

      &:hover::after {
        left: 0%;
        width: 100%;
      }
    }
  }
}

.account-modal {
  max-width: 345px !important;

  .ant-modal-body {
    padding: 0 20px 20px;
  }

  img {
    width: 50px;
    height: 50px;
    margin: 10px auto 16px;
    display: block;
    border-radius: 10px;
  }

  .desc {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    height: 20px;
    color: #0f134f;
    text-align: center;
    margin-bottom: 20px;
  }

  .account-info {
    width: 100%;
    height: 54px;
    background: rgba(61, 86, 214, 0.05);
    border-radius: 6px;
    padding: 16px 8px;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    color: #0f134f;
    margin-bottom: 30px;
    text-align: center;
    position: relative;

    &:hover {
      background: rgba(61, 86, 214, 0.1);
      color: #3d56d6;
      cursor: pointer;
    }
  }

  .disconnect {
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    text-align: center;
    color: #3d56d6;
    cursor: pointer;
  }

  span {
    font-weight: 400;
    font-size: 12px;
    color: #3d56d6;
    margin-left: 6px;
    cursor: pointer;
  }
}

.failure-modal {
  max-width: 345px !important;

  .ant-modal-content {
    height: 340px;
  }

  img {
    width: 60px;
    height: 60px;
    margin: 10px auto 20px;
    display: block;
  }

  .title {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #0f134f;
    height: 25px;
    margin-bottom: 10px;
  }

  .desc {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    height: 20px;
    color: #5a5e89;
    text-align: center;
  }

  .reconnect {
    position: absolute;
    bottom: 20px;
    width: 305px;
    height: 40px;
    background: #3d56d6;
    border-radius: 8px;
    margin: 0 auto;
    font-size: 14px;
    line-height: 40px;
    text-align: center;
    color: #ffffff;
    cursor: pointer;

    &:hover {
      background: rgba(61, 86, 214, 0.3);
    }
  }
}

.copied {
  position: absolute;
  left: 0;
  bottom: -30px;
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: #fff;
  border-radius: 8px;
  font-family: 'PingFang SC';
  font-size: 14px;
  text-align: center;
  color: #5a5e89;
  animation: copied-animation 1s;
}

@keyframes copied-animation {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0.8;
  }

  100% {
    opacity: 1;
  }
}

.accountTooltip {
  margin-top: 26px;

  .ant-tooltip {
    .ant-tooltip-inner {
      padding: 10px 16px;
    }
  }
}
