@import url(./common.scss);
// todo 
iframe {
  display: none !important;
}
$black: #22232b;
$black_light: rgba(34, 35, 43, 0.6);

$white: #ffffff;
$white_light: rgba(255, 255, 255, 0.6);

$section_bg_color: #31333a;
.arrow-right {
  display: inline-block;
  width: 10px;
  height: 4px;
  background: none 0 0 no-repeat;
  position: relative;
  top: -3px;
  margin: 0 4px;
}
.markey-detail-tooltip {
  .ant-tooltip-content .ant-tooltip-inner {
    // background-color: $section_bg_color;
    padding: 12px;
    min-width: 80px;
  }
  * {
    color: #fff;
    font-size: 12px;
    line-height: 20px;
    font-family: 'PingFang SC';
  }
}

.en-US,
.en-US span,
.en-US .label {
  font-family: 'Avenir Next';
}

.ant-tooltip-inner .market-detail-tokenop-tooltip {
  margin: 0 !important;
  .market-detail-tokenop-item {
    display: flex;
    align-items: center;
    & + .market-detail-tokenop-item {
      margin-top: 10px;
    }
    .icon {
      display: block;
      width: 12px;
      height: 12px;
    }
    .text {
      margin-left: 10px;
      color: $white_light;
      font-weight: 400;
      &:hover {
        color: $white;
      }
    }
    &.disabled {
      .text:hover {
        color: $white_light;
      }
    }
    .toTronscan {
      width: 16px;
      height: 12px;
      background: url(../../../images/v2/market-detail/share.svg) no-repeat center;
      display: inline-block;
      position: relative;
      top: -1px;
      &.disabled {
        opacity: 0.6;
        cursor: not-allowed;
      }
      // &.white {
      //   background-image: url(../../../images/v2/white-theme/market-detail/share.svg);
      // }
    }
    .addtoTronLink {
      width: 16px;
      height: 13px;
      background: url(../../../images/mobile/to-tronlink-icon.png) no-repeat 0 0;
      background-size: cover;
      display: inline-block;
      cursor: pointer;
      margin-bottom: 3px;

      &.disabled {
        background: url(../../../images/mobile/to-tronlink-icon-disabled.png) no-repeat 0 0;
        background-size: cover;
        cursor: default;
      }
    }
  }
}
.market-d {
  background-color: transparent;
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;

  // utils
  .color-primary {
    color: $white;
  }
  .color-light {
    color: $white_light;
  }
  .bg-lighter {
    background-color: rgba(255, 255, 255, 0.04);
  }

  .fs12 {
    font-size: 12px;
    line-height: 17px;
  }
  .fs14 {
    font-size: 14px;
    line-height: 19px;
  }
  .fs16 {
    font-size: 16px;
    line-height: 22px;
  }
  .mt-base {
    margin-top: 20px;
  }
  .ml-base {
    margin-left: 20px;
  }
  .flex {
    display: flex;
  }
  .flex-col {
    display: flex;
    flex-direction: column;
  }

  // component
  .section {
    background-color: $section_bg_color;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 20px;
    .title-wrap {
      display: flex;
      align-items: center;
    }
    .title {
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
    }
  }
  .btn {
    // width: 100px;
    font-size: 12px;
    line-height: 21px;
    height: 21px;
    text-align: center;
    border-radius: 2px;
    padding: 0 24px;
    & + .btn {
      margin-left: 20px;
    }
  }

  // 
  .main-price {
    font-family: 'Avenir Next';
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    min-width: 86px;
  }

  .market-d-head {
    padding: 30px 0 22px;
    width: 100%;
    display: flex;
  }
  .market-d-head-mobile {
    display: none;
  }
  .market-d-go-back {
    width: 180px;
    line-height: 28px;
    opacity: 0.6;
    padding-left: 23px;
    background: url(../../../images/v2/market-detail/return.svg) no-repeat 0 center / 20px;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
  }
  .market-d-title {
    line-height: 28px;
    display: flex;
    align-items: center;
    img {
      width: 20px;
      height: 20px;
      object-fit: contain;
    }
    // 
    .market-name {
      font-family: 'Avenir Next';
      font-weight: 600;
      font-size: 20px;
      line-height: 28px;
      margin: 0 0 0 6px;
    }
  }

  .market-d-body {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    width: 100%;
  }

  .market-d-select {
    width: 160px;
    padding: 10px 0 14px;
    overflow: hidden;
    height: 1080px;
    cursor: pointer;
    &.disabled {
      cursor: not-allowed;
    }
    &-item {
      display: flex;
      box-sizing: border-box;
      padding: 20px 0 20px 27px;
      border-left: 3px solid transparent;
      cursor: pointer;
      transition: background-color 0.3s ease-in-out;

      &.active span {
        font-weight: 600;
      }
      &.active,
      &:hover {
        border-left-color: #4c54ff;
        background-color: rgba(255, 255, 255, 0.04);
        span {
          color: $white;
        }
      }
      img {
        height: 26px;
        width: 26px;
      }
      span {
        margin-left: 10px;
        color: $white-light;
        line-height: 26px;
      }
    }
  }
  .market-d-main {
    flex: 1;
    display: flex;
    flex-direction: column;
    .market-d-profile {
      width: 100%;
      display: flex;
      align-items: center;
      padding: 14px 20px 11px 20px;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
      .section-content {
        flex: 1;
        display: flex;
        align-items: center;
        width: auto;
        justify-content: space-between;
      }

      .price-wrap {
        &-inner {
          display: flex;
          align-items: flex-start;
          align-items: flex-end;
          .number-wrap {
            display: flex;
            align-items: center;
            margin-left: 15px;
          }
        }
        .price-tooltip {
          display: flex;
          align-items: center;
          margin-bottom: 5px;
          &-text {
            margin-right: 6px;
          }
        }
        .exchange-btn {
          background: rgba(255, 122, 138, 0.06);
          border-radius: 2px;
          color: #ff7a8a;
          line-height: 21px;
          padding: 0 13px;
          margin: 0 15px 0 10px;
        }
      }

      // copy from ../market.scss
      .j-token-info {
        display: flex;
        align-items: center;
        background: #282931;
        padding: 3px 15px;
        margin-left: 20px;
        .token-wrap {
          cursor: pointer;
          display: flex;
          align-items: center;
          .token-img {
            width: 14px;
            height: 14px;
            border-radius: 100%;
          }
          .token-text {
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            margin-left: 6px;
            opacity: 0.4;
            &:hover {
              opacity: 1;
            }
          }
        }
        .divider {
          height: 12px;
          width: 1px;
          background-color: rgba(255, 255, 255, 0.12);
          margin: 0 18px;
        }
      }
      .flex-between {
        padding: 0px;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        border-radius: 4px;
        img {
          width: 16px;
          height: 16px;
          vertical-align: top;
        }
        .aic {
          margin-left: 16px;
        }
      }
    }
    // 
    &-content {
      display: flex;
      flex-direction: column;
      flex: 4 1 660px;

      // 
      .interest-rate-model {
        padding-bottom: 17px;
        .chart-tooltip-fake {
          display: none;
        }
        .title-wrap {
          display: flex;
          justify-content: space-between;
          align-items: center;

          // checkbox
          .ant-checkbox {
            font-size: 12px;
            width: 12px;
            height: 12px;
            top: 0.1em;
            .ant-checkbox-inner {
              width: 12px;
              height: 12px;
            }
          }
          .ant-checkbox-checked .ant-checkbox-inner {
            background-color: #4c54ff;
            border-color: #4c54ff;
          }

          .ant-checkbox-checked .ant-checkbox-inner::after {
            border-width: 1px;
            transform: rotate(45deg) scale(0.7) translate(-80%, -60%);
          }
        }
        .legend-wrap {
          display: flex;
          justify-content: space-between;
          padding-top: 20px;
          .legend {
            .legend-item {
              align-items: center;
            }
            .legend-item + .legend-item {
              margin-left: 20px;
            }
          }
          .square {
            display: block;
            width: 8px;
            height: 8px;
            margin-right: 6px;
          }
          .deposit {
            background-color: #9195fb;
          }
          .borrow {
            background-color: #18c19f;
          }
        }
      }
      .market-detail-echarts-v2 {
        padding: 0;
      }

      // tooltip
      .chart-tooltip {
        width: 300px;
        box-sizing: border-box;
        border-radius: 2px;
        header {
          display: flex;
          justify-content: space-between;
          font-weight: 500;
          line-height: 20px;
          padding: 10px 10px 10px 15px;
          border-bottom: 1px solid rgba(255, 255, 255, 0.06);
        }
        header .value {
          font-family: 'Avenir Next';
          font-size: 14px;
          font-weight: 600;
          line-height: 20px;
        }
        main {
          padding: 10px 10px 10px 15px;
        }
        .item {
          display: flex;
          justify-content: space-between;
          .label {
            font-size: 12px;
            line-height: 17px;
            display: flex;
            .icon {
              display: block;
              width: 14px;
              height: 14px;
              margin-right: 6px;
              background: none no-repeat center/contain;
            }
            .icon-line-chart {
              background-image: url(../../../images/v2/market-detail/chart-line.svg);
              &.white {
                background-image: url(../../../images/v2/white-theme/market-detail/chart-line.svg);
              }
            }
            .icon-bar-chart {
              background-image: url(../../../images/v2/market-detail/chart-bar.svg);
              &.white {
                background-image: url(../../../images/v2/white-theme/market-detail/chart-bar.svg);
              }
            }

            &.en-US {
              display: inline-flex;
              flex-direction: column;
            }
            span {
              white-space: normal;
              word-break: break-all;
            }
          }
          .value-wrap {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            font-family: 'Avenir Next';
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            .deposit {
              color: #9195fb;
              display: inline-flex;
              align-items: center;
              .fire-icon {
                margin-left: 5px;
              }
            }
            .borrow {
              color: #18c19f;
            }
            .detail {
              white-space: normal;
              word-break: break-all;
            }
          }
        }
        .item + .item {
          margin-top: 10px;
        }
      }
      .interest-rate {
        .fire-icon,
        .mint-text,
        .detail {
          display: none;
        }
        .include-mint {
          .mint-text,
          .detail,
          .fire-icon {
            display: inline;
          }
        }
      }
    }
    // 
    &-data {
      box-sizing: border-box;
      flex: 1 1 340px;
      // justify-self: flex-end;
      .mint-info-link {
        color: #9195fb;
        display: inline-flex;
        align-items: center;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        &:hover {
          text-decoration: underline;
        }
        i {
          display: block;
          width: 8px;
          height: 8px;
          margin-left: 6px;
          background: url(../../../images/v2/market-detail/arrow-right.svg) no-repeat center/contain;
        }
      }
      .mint-apy-info {
        padding: 10px 11px;
        margin-top: 10px;
        background-color: rgba(255, 255, 255, 0.04);
        .item .value {
          font-size: 12px;
          line-height: 16px;
        }
        & + .item {
          margin-top: 10px;
        }
      }

      .item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        & + .item {
          margin-top: 20px;
        }
        .label {
          display: flex;
          align-items: center;
          &,
          & span {
            font-size: 12px;
            line-height: 17px;
          }
          .icon {
            margin-left: 6px;
          }
        }
        .value {
          font-family: 'Avenir Next';
          font-weight: 600;
          font-size: 16px;
          line-height: 19px;
          text-align: right;
          &.strong {
            font-size: 16px;
            line-height: 22px;
          }
          &.deposit {
            color: #9195fb;
            .icon {
              margin-left: 6px;
            }
          }
          &.borrow {
            color: #18c19f;
          }
          &.reward {
            color: #e9b361;
          }
        }
      }
      .fire-icon {
        display: inline-block;
        flex: 0 0 auto;
        width: 12px;
        height: 14px;
        background: url(../../../images/mobile/fire.svg) no-repeat center/contain;
      }
      .notice {
        padding: 13px 20px;
        margin: 20px -20px 0 -20px;
        background-color: rgba(255, 255, 255, 0.04);
        display: flex;
        align-items: flex-start;
        font-weight: 500;
        span {
          margin-left: 5px;
          line-height: 20px;
        }
        .fire-icon {
          height: 20px;
        }
      }
      .divider-h {
        height: 1px;
        background-color: $white_light;
        opacity: 0.1;
      }
    }
  }
}
// question-icon
.question-icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  background: url(../../../images/v2/tooltip-icon.svg) no-repeat center/contain;
  opacity: 0.6;
  cursor: pointer;
  &:hover {
    opacity: 1;
    // background-image: url(../../../images/v2/tooltip-icon-hover.svg);
  }
}

// 
.j-wrapper.white {
  .market-d {
    background-color: #f0f2f7;

    .color-primary {
      color: $black;
    }
    .color-light {
      color: $black_light;
    }
    .section {
      background-color: #ffffff;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      .title {
        text-shadow: none;
      }
    }
  }
  .market-d .market-d-go-back {
    background-image: url(../../../images/v2/white-theme/market-detail/return.svg);
  }
  .question-icon {
    background-image: url(../../../images/v2/white-theme/market-detail/tooltip-icon.svg);
  }

  .notice {
    background-color: rgba(77, 84, 255, 0.06);
  }
  .market-d .market-d-main-data {
    .mint-apy-info {
      background-color: rgba(77, 84, 255, 0.06);
    }
  }
  .market-d .market-d-main-data .divider-h {
    background-color: rgba(34, 35, 43, 0.6);
  }
  .market-d .market-d-main .market-d-profile .j-token-info {
    background-color: rgb(75, 82, 223, 0.06);
    .divider {
      background-color: rgba(34, 35, 43, 0.1);
    }
  }

  .btn-wrap {
    .j-btn.j-supply {
      color: #4d54ff;
      background: transparent;
      &:hover {
        background: #4d54ff;
      }
    }
    .j-btn.j-borrow {
      background: transparent;
      border-color: #18c19f;
      &:hover {
        background: #18c19f;
      }
    }
  }

  // 
  .market-d-select {
    &-item {
      &.active,
      &:hover {
        background-color: rgba(76, 84, 255, 0.1);
        span {
          color: $black;
        }
      }
      span {
        color: $black_light;
      }
    }
  }

  .market-d .market-d-main-content .chart-tooltip {
    box-shadow: 0px 8px 20px rgba(15, 19, 79, 0.06);
    border-radius: 2px;
    header {
      border-bottom-color: rgba(0, 0, 0, 0.05);
    }
  }
}

@media screen and (max-width: 768px) {
  .market-d {
    padding: 10px 15px;
    .ml-base {
      margin-top: 15px;
      margin-left: 0;
    }
    .mt-base {
      margin-top: 15px;
    }
    & .section {
      background-color: transparent;
      padding: 0;
      margin-top: 15px;
      .title {
        padding-left: 15px;
        font-size: 14px;
        line-height: 20px;
        font-weight: 600;
      }
      .section-content {
        background-color: #282931;
        padding: 13px 15px 15px;
        margin-top: 10px;
      }
    }
    & &-head {
      display: none;
    }
    & &-head-mobile {
      display: flex;
      align-items: center;
      margin: 0px 0 10px;
      .indicator {
        width: 5px;
        height: 13px;
        background-color: #4d54ff;
        border-radius: 1px;
      }
      .text {
        margin-left: 10px;
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        &.en-US {
          font-family: 'Avenir Next';
        }
      }
    }
    &-body {
      flex-direction: column;
    }
    & &-select {
      display: flex;
      width: auto;
      overflow-x: auto;
      padding: 0;
      background-color: #282931;
      margin-top: 0;
      &-inner {
        width: 100%;
        display: flex;
      }
      &-item {
        flex-direction: column;
        align-items: center;
        padding: 14px 0 15px;
        width: 65px;
        flex: 0 0 65px;
        border-top: 1px solid transparent;
        span {
          margin: 10px 0 0;
          line-height: 19px;
        }
        &.active {
          border-left: none;
          border-top: 1px solid #4c54ff;
        }
      }
    }
    & &-main {
      margin-top: 20px;
      & .market-d-profile {
        flex-direction: column;
        align-items: stretch;
        padding: 0px;
        margin-top: 0;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
        .section-content {
          margin-top: 0;
          flex-direction: column;
          align-items: stretch;
          padding: 0;
        }
        .price-wrap {
          .flex {
            flex-direction: column;
          }
          .price-wrap-inner {
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            padding: 0px 15px 0;
            border-bottom: 1px solid rgba(255, 255, 255, 0.06);
            .number-wrap {
              flex: 1;
              margin-left: 10px;
              display: flex;
              justify-content: space-between;
              .main-price {
                font-weight: 600;
                font-size: 18px;
                line-height: 50px;
              }
            }
            .exchange-btn {
              margin: 0;
            }
          }
          .price-tooltip {
            flex-direction: row-reverse;
            margin: 0;
            .price-tooltip-text {
              margin: 0 0 0 6px;
            }
          }
          .j-token-info {
            flex-direction: row;
            margin-top: 0;
            height: 36px;
            align-items: center;
            .divider {
              display: none;
            }
            .flex-between {
              flex: 1 1 50%;
              justify-content: center;
              .aic {
                flex-direction: row;
              }
            }
          }
        }
        .btn-wrap {
          margin-top: 0px;
          justify-content: center;
          padding: 0 15px 15px;
          .btn {
            flex: 1;
          }
          .btn + .btn {
            margin-left: 31px;
          }
          .j-btn.j-supply:hover {
            background: #4d54ff;
          }
          .j-btn.j-borrow {
            background: #18c19f;
          }
        }
      }
      .market-d-main-content-wrap {
        flex-direction: column-reverse;
        .market-d-main-data {
          margin-top: 0;
          .section-content {
            padding-top: 0;
            overflow: hidden;
            & > .mt-base,
            .notice {
              margin-top: 12px;
              padding: 9px 20px;
              span {
                font-size: 10px;
              }
            }
            & > .mt-base {
              padding: 0 20px 0;
            }
            .item + .item {
              margin-top: 12px;
            }
            .item {
              padding-left: 0;
              padding-right: 0;
            }
            .divider-h {
              padding: 0;
            }
            .divider-h,
            .divider-h + .item {
              margin-top: 15px;
            }
          }
        }
      }
    }
  }

  // 
  .interest-rate-model.section .section-content {
    padding: 0;
    .chart-tooltip {
      width: auto;
      padding-left: 15px;
      padding-right: 15px;
      box-shadow: none;

      header,
      main {
        padding-left: 0;
        padding-right: 0;
      }
      header {
        padding: 15px 0px 15px 0;
      }
    }
    .chart-tooltip-fake {
      display: block;
      padding: 0;
      header,
      main {
        margin-top: 0;
        padding-left: 15px;
        padding-right: 15px;
      }
    }
    .market-detail-echarts-v2 {
      margin-top: 0;
    }
    .legend-wrap {
      padding: 10px 15px 10px;
      background-color: rgba(255, 255, 255, 0.06);
      .fs12 {
        font-size: 10px;
        line-height: 14px;
      }
    }
  }

  .j-wrapper.white {
    .market-d {
      .section {
        background-color: transparent;
        .section-content {
          background-color: #ffffff;
        }
      }
      .market-d-select {
        background-color: #ffffff;
      }
      .market-d-main .market-d-profile .price-wrap-inner {
        border-bottom: 1px solid rgba(34, 35, 43, 0.06);
      }
      .market-d-main .chart-tooltip {
        box-shadow: none;
        background-color: rgba(250, 251, 255, 0.8);
      }
      .interest-rate-model.section .section-content {
        .legend-wrap {
          background-color: rgba(255, 255, 255);
        }
      }
    }
  }
}

@media screen and (min-width: 1700px) {
  .market-d {
    max-width: 1440px;
  }
}
